import React, { Component } from 'react';
import Result from './components/Result'
import './App.css';
/*import  Example  from './components/Example';*/
import  SearchBar from './components/SearchBar';
import Timetable2 from './components/Timetable2';
import moment from "moment";
import {Container} from 'reactstrap';

import Modal from 'react-modal';

class App extends Component {

  constructor() {
    super();
    var events= {
      monday: [
        {
          id: 1,
          name: '軟體工程',
          type: 'custom',
          location: '(大智樓)',
          startTime: moment('2000-01-01T13:00:00'),
          endTime: moment('2000-01-01T16:00:00'),
          priority: 1
        }
      ],

      tuesday: [
        {
          id: 2,
          name: 'aaa',
          type: 'custom',
          location: '(大仁樓)',
          startTime: moment('2000-01-01T08:00:00'),
          endTime: moment('2000-01-01T11:00:00'),
          priority: 3
        },

        {
          id: 1,
          name: 'bbb',
          type: 'custom',
          location: '(大仁樓)',
          startTime: moment('2000-01-01T10:00:00'),
          endTime: moment('2000-01-01T12:00:00'),
          priority: 2
        },

        {
          id: 1,
          name: 'ccc',
          type: 'custom',
          location: '大仁樓',
          startTime: moment('2000-01-01T11:00:00'),
          endTime: moment('2000-01-01T12:00:00'),
          priority: 4
        },
      ],
      wednesday: [],
      thursday: [],
      friday: []
    };

    this.state = {
      events : events,
    }

  }
  componentDidMount() {
      Modal.setAppElement(this.el);
  }


  render() {
    return (
      <div ref={ref => this.el = ref}>
        <SearchBar/>
        <Timetable2 events={this.state.events}/>
        <Container>
          <Result/>
          <div>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
            <p> 1 </p>
          </div>
        </Container>
      </div>
    );
  }
}

export default App;



